.header {
    width: 100%;
    height: 5rem;
    background-color: #30355a;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  }

  h1 > em {
    color: #df5549;
    font-style: normal;
  }

  @media screen and  (max-width: 800px){
    .header {
      flex-direction: column;
      height: auto;
      gap: 1rem;
      padding-bottom: 1rem;
    }

    .header h1 {
      margin-top: 1rem;
      font-size: 1.5rem;
    }
  }